<template>
  <div>
    <h1>注册界面</h1>
    <van-form validate-first @failed="onFailed" @submit="onSubmit">
  <!-- 通过 pattern 进行正则校验 -->
 
  <van-field
    v-model="nickname"
    name="pattern"
    label="昵称"
    placeholder="起个昵称吧"
    :rules="[{ required: true, message: '请输入内容' }]"
  />
  <van-field
    v-model="username"
    name="pattern"
    label="用户名"
    placeholder="请输入用户名,仅限英文和字母组合"
    :rules="[{ pattern:usernamePattern, message: '5-16位英文和字母组合' }]"
  />
  <!-- 通过 validator 进行函数校验 -->
  <van-field
    v-model="password"
    name="pattern"
    label="密码"
    placeholder="输入密码,最长16位哦"
    type="password"
    :rules="[{ pattern:passwordPattern, message: '请输入正确内容' }]"
  />
  
  <!-- 通过 validator 进行异步函数校验 -->
   <van-field
    v-model="rePassword"
    name="pattern"
    label="确认密码"
    type="password"
    placeholder="再次输入密码"
    :rules="[{ required: true, message: '请输入内容' }]"
  />
  <div style="margin: 16px;">
    <van-button round block type="info" native-type="submit">提交</van-button>
  </div>
</van-form>

  </div>
</template>
<script>
import axios from 'axios'
import { Notify } from 'vant';
// import { Toast } from 'vant';
export default {
  name: 'MyRegister',
  data() {
    return {
      username: '',
      password: '',
      rePassword: '',
      nickname: '', 
      usernamePattern: /^[A-Za-z0-9]{5,16}$/,
      passwordPattern: /.{6,16}/,

    }
  },

  methods: {
    /**
     * 校验通过了才会执行onSubmit中的内容
    */
     onSubmit() {
      this.register()
      
    },

   
    onFailed(errorInfo) {
      console.log('failed', errorInfo);
    },
    
    register() {
      let regx = /^[A-Za-z0-9]+$/
      if (regx.test(this.username)) {
        if (
          this.username == '' ||
          this.password == '' ||
          this.rePassword == '' ||
          this.nickname == ''
        ) {
          
          Notify({ type: 'warning', message: '还有没填的信息' });
        } else {
          if (this.rePassword !== this.password) {
           
            Notify({ type: 'warning', message: '密码不一致,请重新输入' });

            this.password = ''
            this.rePassword = ''
          } else {
            axios({
              method: 'post',
              url: this.$baseURL+'/user/register',
              data: {
                username: this.username,
                password: this.password,
                nickname: this.nickname,
              },
            }).then((res) => {
              if (res.data.status == 200) {
                alert('注册成功,快去登录吧')
                this.$router.push('/phone/login')
              }
            })
          }
        }
      } else {
        
        Notify({ type: 'warning', message: '用户名不符合规则' });
        this.username = ''
      }
    },
  },
  mounted(){
   
    
    
  }
}
</script>
